/* navbar and navbutton styles */

.navbar-container {
  padding-left: 20px;
  padding-right: 20px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  background-color: #253263;
  flex-direction: row;
}
.mobnav-custom{
  background: #253263;
}
.mobnav-container{
  background-color: #253263;
  display: flex;
  flex-direction: column
}
.menu-button{
  margin: 15px;
  height: 20px;
  width: 30px;
  margin-right: 20px
}
.mobnav-top-row{
    width: 100vw;
    justify-content: space-between;
    display: flex;
    align-items: center;
}
.mobnav-buttons-container{
    margin: 20px;
    margin-top: 0px;
}

.collapse-container{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.logo {
  margin: 5px;
  height: 35px;
}
.mobnav-logo {
  margin: 10px;
  height: 35px;
}
.logo:hover{
  cursor: pointer;
}
.nav-button-selected {
  color: #ffffff;
}
.nav-button-unselected{
  color: #bdbdbd;
}
.nav-button {
  font-style: normal;
  margin-right: 10px;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  font-size: 18px;
}
.nav-button:hover{
    cursor: pointer;
}
.nav-button-m:hover{
    cursor: pointer;
}
.nav-button-m {
  font-style: normal;
  margin-right: 5%;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  font-size: 25px;
}
.nav-button:hover {
  cursor: pointer;
}
.nav-buttons {
  padding-right: 10px;;
  height: 50px;
  justify-content: space-evenly;
  align-items: center;
  font-size: 18px;
  line-height: 29px;
  display: flex;
  flex-direction: row;
}
